import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { AccessToHEDiplomaComponent } from '../access-to-hediploma/access-to-hediploma.component';
import { ContactUsComponent } from '../contact-us/contact-us.component';
import { MultipleQualificationComponent } from '../multiple-qualification/multiple-qualification.component';
import { UcasTariffPointsComponent } from '../ucas-tariff-points/ucas-tariff-points.component';

@Component({
  selector: 'app-qualification',
  templateUrl: './qualification.component.html',
  styleUrls: ['./qualification.component.scss'],
})
export class QualificationComponent implements OnInit {

  constructor(private modalCtrl: ModalController) { }

  ngOnInit() {}

  async openUcasTariffPointsModal(){
    const modal = await this.modalCtrl.create({
      component: UcasTariffPointsComponent,
      cssClass: 'my-modal-styles'
    });
    await modal.present();
  }
  async openMultipleQualificationModal(){
    const modal = await this.modalCtrl.create({
      component: MultipleQualificationComponent,
      cssClass: 'my-modal-styles'
    });
    await modal.present();
  }
  async openAccesstoHEDiplomaModal(){
    const modal = await this.modalCtrl.create({
      component: AccessToHEDiplomaComponent,
      cssClass: 'my-modal-styles'
    });
    await modal.present();
  }
  modaldismiss(){
    this.modalCtrl.dismiss();
  }

}
