import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { ContactUsComponent } from '../../common/contact-us/contact-us.component';
import { UpdatePasswordComponent } from '../../common/update-password/update-password.component';
import { YourGradesComponent } from '../../common/your-grades/your-grades.component';
import { InAppBrowser, InAppBrowserOptions } from '@awesome-cordova-plugins/in-app-browser/ngx';


@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.page.html',
  styleUrls: ['./user-profile.page.scss'],
})
export class UserProfilePage implements OnInit {

  constructor(private router: Router, private modalCntrl: ModalController,private iab: InAppBrowser) { }

  ngOnInit() {
  }
  gotoPersonalDetails() {
    this.router.navigate(['/personal-detail']);
  }
  gotoStudyDetails() {
    this.router.navigate(['/study-details']);
  }
  gotoUserSettings() {
    this.router.navigate(['/user-settings']);
  }
  gotoLocationServices() {
    this.router.navigate(['/location-services']);
  }
  gotoNotifications() {
    this.router.navigate(['/notifications']);
  }
  async openYourGradesModal() {
    const modal = await this.modalCntrl.create({
      component: YourGradesComponent,
      cssClass: 'my-modal-styles'
    });
    await modal.present();
  }
  async openUpdatePwdModal() {
    const modal = await this.modalCntrl.create({
      component: UpdatePasswordComponent,
      cssClass: 'my-modal-styles'
    });
    await modal.present();
  }
  async openContactUsModal() {
    const modal = await this.modalCntrl.create({
      component: ContactUsComponent,
      cssClass: 'my-modal-styles'
    });
    await modal.present();
  }
  openPlaystore() {
    // this.market.open('com.hotcourses.group.wuapp');
    let options:InAppBrowserOptions = {
      closebuttoncaption:'X',
      clearcache: 'yes',
      location: 'yes',
      hardwareback: 'no',
      footer: 'no',
      hidenavigationbuttons: 'yes',
      hideurlbar: 'yes',
      toolbarcolor: '#333333',
      toolbarposition: 'top'
    }
    this.iab.create('https://play.google.com/store/apps/details?id=com.hotcourses.group.wuapp','',options)
  }
  openInapp(eve:string) {
    let options:InAppBrowserOptions = {
      closebuttoncaption:'X',
      clearcache: 'yes',
      location: 'yes',
      hardwareback: 'no',
      footer: 'no',
      hidenavigationbuttons: 'yes',
      hideurlbar: 'yes',
      toolbarcolor: '#333333',
      toolbarposition: 'top'
    }
    this.iab.create(eve,'',options)
  }
}
