import { Component, OnInit, PlatformRef } from '@angular/core';
import { OpenNativeSettings } from '@awesome-cordova-plugins/open-native-settings/ngx';
import { Diagnostic } from '@awesome-cordova-plugins/diagnostic/ngx';
import { Platform } from '@ionic/angular';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.page.html',
  styleUrls: ['./notifications.page.scss'],
})
export class NotificationsPage implements OnInit {
  public notificationEnable: boolean =false;
  constructor(private openNativeSettings: OpenNativeSettings,private diagnostic: Diagnostic,private platform:Platform) { }

  ngOnInit() {
    this.diagnostic.isRemoteNotificationsEnabled().then(res =>{
      this.notificationEnable = res;
    }, err=>{
      console.log(err);
    });
    this.platform.resume.subscribe(async () => {
      this.diagnostic.isRemoteNotificationsEnabled().then(res =>{
        this.notificationEnable = res;
      }, err=>{
        console.log(err);
      });
    });

  }
  toggleChange(eve:Event) {
      this.openNativeSettings.open('notification_id').then(res =>{
        console.log(res);
      }, err =>{
        console.log(err);
      })
    
  }

}
