import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { StudyLevelComponent } from '../../common/study-level/study-level.component';
import { YearOfEntryComponent } from '../../common/year-of-entry/year-of-entry.component';

@Component({
  selector: 'app-study-details',
  templateUrl: './study-details.page.html',
  styleUrls: ['./study-details.page.scss'],
})
export class StudyDetailsPage implements OnInit {

  constructor(private router: Router, private modalCntrl: ModalController) { }

  ngOnInit() {
  }

    async openStudyLevelModal(){
      const modal = await this.modalCntrl.create({
        component: StudyLevelComponent,
        cssClass: 'my-modal-styles'
      });
      await modal.present();
    }
    async openYearofEntryModal(){
      const modal = await this.modalCntrl.create({
        component: YearOfEntryComponent,
        cssClass: 'my-modal-styles'
      });
      await modal.present();
    }
    
    
    
}
