import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { QualificationComponent } from '../qualification/qualification.component';

@Component({
  selector: 'app-your-grades',
  templateUrl: './your-grades.component.html',
  styleUrls: ['./your-grades.component.scss'],
})
export class YourGradesComponent implements OnInit {

  constructor(private modalCtrl: ModalController) { }

  ngOnInit() {}

  async openQualificationModal(){
    const modal = await this.modalCtrl.create({
      component: QualificationComponent,
      cssClass: 'my-modal-styles'
    });
    await modal.present();
  }

  modaldismiss(){
    this.modalCtrl.dismiss();
  }

}
