import { Component, OnInit } from '@angular/core';
import { OpenNativeSettings } from '@awesome-cordova-plugins/open-native-settings/ngx';
import { Diagnostic } from '@awesome-cordova-plugins/diagnostic/ngx';
import { Platform } from '@ionic/angular';
@Component({
  selector: 'app-location-services',
  templateUrl: './location-services.page.html',
  styleUrls: ['./location-services.page.scss'],
})
export class LocationServicesPage implements OnInit {
public locationEnable:boolean = false;
  constructor(private openNativeSettings: OpenNativeSettings,private diagnostic: Diagnostic,private platform:Platform) { }


  ngOnInit() {
    this.diagnostic.isLocationEnabled().then(res =>{
      this.locationEnable = res;
    }, err=>{
      console.log(err);
    });
    this.platform.resume.subscribe(async () => {
      this.diagnostic.isLocationEnabled().then(res =>{
        this.locationEnable = res;
      }, err=>{
        console.log(err);
      });
    });

  }
  toggleChange(eve:Event) {
      this.openNativeSettings.open('location').then(res =>{
        console.log(res);
      }, err =>{
        console.log(err);
      })
    
  }

}
