import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-personal-detail',
  templateUrl: './personal-detail.page.html',
  styleUrls: ['./personal-detail.page.scss'],
})
export class PersonalDetailPage implements OnInit {

  profileForm: FormGroup;
  errMsgs: Array<string> = [];

  constructor(private formBuilder: FormBuilder) {
    this.profileForm = this.formBuilder.group({
      firstName: ['', [Validators.required]],    // Validators.pattern('[a-zA-Z ]*')
      lastName: ['', [Validators.required]],
    });
  }

  ngOnInit() {
  }

  save() {
    let errMsgs = [];
    if (!this.profileForm.get('firstName')?.valid) {
      errMsgs.push('First name is required');
    } if (!this.profileForm.get('lastName')?.valid) {
      errMsgs.push('Last name is required');
    }
    this.errMsgs = errMsgs;
  }

}
